<template>
  <div style="height:100%">
    <Navbar name="" />
    <van-notice-bar
      v-if="showNotice"
      :text="notice.content"
      left-icon="volume-o"
      mode="link"
      @click="gotoArticle(notice.id)"
    />
    <swiper ref="mySwiper" style="height: 120px">
      <!-- slides -->
      <swiper-slide v-for="item in banner" :key="item.pic">
        <van-image width="100%" height="120px" :src="domain + item.pic" fit="cover" />
      </swiper-slide>
    </swiper>
    <template v-if="setting.modeType == 2">
      <van-sticky>
        <div class="switchItem">
          <van-dropdown-menu :active-color="color">
            <van-dropdown-item v-model="nowIndexb" :options="category" :title="nowName" />
          </van-dropdown-menu>
          <div class="youhui" v-if="youhui.length" @click="showYouhuiVisible=true">
            <van-tag plain type="warning" :color="color">{{youhui[0]}}</van-tag>
            <van-tag plain type="warning" :color="color"></van-tag>
          </div>
          <div class="sortWrap">
            <span
              v-for="(item,index) in sortConfig"
              :key="JSON.stringify(item)"
              :style="item.active && baseColor"
              @click="sort(index)"
            >
              {{item.name}}
              <van-icon :name="item.icon" />
            </span>
          </div>
        </div>
      </van-sticky>
      <div class="productList clearfix">
        <div class="productItem" v-for="item in nowProducts" :key="JSON.stringify(item)">
          <van-image
            @click="showDetail(item)"
            width="100%"
            height="180"
            fit="cover"
            :src="getZhutu(item)"
          />
          <div class="text">
            <p class="title">
              {{item.name}}
              {{item.unit}}
            </p>
            <p class="note" v-if="item.info">{{item.info}}</p>
            <p class="note" v-else>{{item.name + item.unit}}</p>

            <div class="priceAndNums">
              <p class="price" :style="baseColor">{{item.price}}</p>
              <div class="numbers" v-if="item.num > 0">
                <template v-if="item.selNum > 0">
                  <i class="basecolor iconfont icon-icon_roundreduce_fil" :style="baseColor" @click="numRed(item)"></i>
                  <span>{{item.selNum}}</span>
                </template>
                <i class="basecolor iconfont icon-icon_roundadd_fill" :style="baseColor" @click="numAdd(item)"></i>
              </div>
              <van-tag v-else></van-tag>
            </div>
          </div>
        </div>
      </div>
    </template>
    <template v-if="setting.modeType == 1">
      <div class="modeType1-search-user clearfix">
        <div class="mt1-search" @click="goto('/search')">
          <van-search v-model="value" class="search" round disabled placeholder="" />
        </div>
        <div class="mt1-user" @click="goto('/user')">
          <i class="iconfont icon-yonghu1"></i> 
        </div>
      </div>
      <div class="modeType1">
        <van-sidebar v-model="nowIndexb" class="sidebar">
          <van-sidebar-item
            :title="item.name"
            v-for="item in category"
            :key="JSON.stringify(item)"
          />
        </van-sidebar>
        <div class="modeType1-right">
          <div class="switchItem">
            <div class="youhui" v-if="youhui.length" @click="showYouhuiVisible=true">
              <van-tag plain type="warning" :color="color">{{youhui[0]}}</van-tag>
              <van-tag plain type="warning" :color="color"></van-tag>
            </div>
            <div class="sortWrap">
              <span
                v-for="(item,index) in sortConfig"
                :key="JSON.stringify(item)"
                :class="[item.active&&'active']"
                @click="sort(index)"
              >
                {{item.name}}
                <van-icon :name="item.icon" />
              </span>
            </div>
          </div>
          <div class="productList" ref="productList">
            <template v-for="(item,index) in nowProducts">
              <div class="productItem clearfix" :key="JSON.stringify(item)">
                <van-image
                  @click="showDetail(item)"
                  width="75"
                  height="75"
                  fit="cover"
                  :src="getZhutu(item)"
                  class="image"
                />
                <div class="text">
                  <p class="title">
                    {{item.name}}
                    {{item.unit}}
                  </p>
                  <p class="note" v-if="item.info">{{item.info}}</p>
                  <p class="note" v-else>{{item.name + item.unit}}</p>

                  <div class="priceAndNums">
                    <p class="price" :style="baseColor">{{item.price}}</p>
                    <div class="numbers" v-if="item.num > 0">
                      <template v-if="item.selNum > 0">
                        <i
                          class="basecolor iconfont icon-icon_roundreduce_fil"
                          :style="baseColor"
                          @click="numRed(item)"
                        ></i>
                        <span>{{item.selNum}}</span>
                      </template>
                      <i class="basecolor iconfont icon-icon_roundadd_fill" :style="baseColor" @click="numAdd(item)"></i>
                    </div>
                    <van-tag v-else></van-tag>
                  </div>
                </div>
              </div>
              <van-divider v-if="index < nowProducts.length - 1" :key="JSON.stringify(item)" />
            </template>
          </div>
        </div>
      </div>
      <div class="mt1-footer" @click="goto('/cartlist')">
        <div class="mt1-cart">
          <i class="iconfont icon-dianpu" :style="baseColor"></i>
          <i class="cartNums" v-if="cartNums > 0">{{cartNums}}</i>
          <span v-if="totalPrice > 0" class="totalPrice">{{totalPrice}}</span>
          <span v-else></span>
        </div>
        <p :style="baseBgColor"></p>
      </div>
    </template>
    <van-popup
      v-model="showDetailVisible"
      round
      position="bottom"
      :style="{ height: '96%' }"
      closeable
      close-icon-position="top-left"
      
    >
      <div class="detailModal">
        <swiper ref="mySwiper" style="height: 220px">
          <!-- slides -->
          <swiper-slide v-for="item in nowProduct.pics" :key="item">
            <van-image width="100%" height="100%" fit="cover" :src="domain + item" />
          </swiper-slide>
        </swiper>
        <div class="text detailText">
          <p class="title">
            <span>{{nowProduct.name}}</span>
            {{nowProduct.unit}}
          </p>
          <p class="note" v-if="nowProduct.info">{{nowProduct.info}}</p>
          <div class="priceAndNums">
            <p class="price" :style="baseColor">{{nowProduct.price}}</p>
            <div class="numbers" v-if="nowProduct.num > 0">
              <template v-if="nowProduct.selNum > 0">
                <i class="basecolor iconfont icon-icon_roundreduce_fil" :style="baseColor" @click="numRed(nowProduct)"></i>
                <span>{{nowProduct.selNum}}</span>
              </template>
              <i class="basecolor iconfont icon-icon_roundadd_fill" :style="baseColor" @click="numAdd(nowProduct)"></i>
            </div>
            <van-tag v-else></van-tag>
          </div>
        </div>
        <div class="detailDetail">
          <h3></h3>
          <div v-html="nowProduct.content"></div>
        </div>
      </div>
    </van-popup>

    <van-popup
      v-model="showYouhuiVisible"
      closeable
      position="bottom"
      round
      :style="{ height: '30%' }"
    >
      <div class="youhuiModal">
        <h4></h4>
        <div class="youhuiItem">
          <van-tag type="warning" size="large"></van-tag>
          <template v-for="item in youhui">
            <van-tag
              type="danger"
              :key="JSON.stringify(item)"
              plain
              v-if="item.indexOf('')>-1"
            >{{item}}</van-tag>
          </template>
        </div>
        <div class="youhuiItem">
          <van-tag type="warning" size="large"></van-tag>
          <template v-for="item in youhui">
            <van-tag
              type="danger"
              :key="JSON.stringify(item)"
              plain
              v-if="item.indexOf('')>-1"
            >{{item}}</van-tag>
          </template>
        </div>
      </div>
    </van-popup>

    <!--  -->
    <!-- <van-tabbar v-model="activeFooter">
      <van-tabbar-item v-for="item in footer" :key="item.icon" replace @click="goto(item.to)">
        <span>{{item.name}}</span>
        <img slot="icon" :src="item.icon" />
      </van-tabbar-item>
    </van-tabbar>-->
    <div class="homeFooter" v-if="setting.modeType == 2">
      <div class="item" v-for="(item,index) in footer" :key="item.icon" @click="goto(item.to)">
        <span class="cartNums" v-if="index == 2 && cartNums > 0" :style="baseBgColor">{{cartNums}}</span>
        <van-image width="20" height="20" :src="item.icon"></van-image>
        <p>{{item.name}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { Notify } from "vant";
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import { mapState, mapMutations } from "vuex";
import Navbar from "./Navbar";

export default {
  name: "Home",
  components: {
    swiper,
    swiperSlide,
    Navbar
  },
  data() {
    return {
      banner: [],
      category: [],
      nowName: "",
      nowProducts: [],
      showDetailVisible: false,
      nowProduct: {},
      youhui: [],
      showYouhuiVisible: false,
      nowIndexb: 0,
      notice: {},
      showNotice: false,
      value:'',
      footer: [
        {
          icon: require("../..//public/images/md2-ft-1.png"),
          name: "",
          to: "/"
        },
        {
          icon: require("../..//public/images/md2-ft-2.png"),
          name: "",
          to: "/search"
        },
        {
          icon: require("../..//public/images/md2-ft-3.png"),
          name: "",
          to: "/cartlist"
        },
        {
          icon: require("../..//public/images/md2-ft-4.png"),
          name: "",
          to: "/user"
        }
      ],
      activeFooter: 0
    };
  },
  created() {
    this.getBanner();
    this.getCategory();
    this.getNotice();
  },
  mounted() {
    this.swiper.slideTo(3, 1000, false);
  },
  methods: {
    ...mapMutations(["cartListSave", "sortConfigSave", "nowIndexSave"]),
    getBanner() {
      this.$axios.get("/getBanner").then(res => {
        const { data } = res;
        if (data.status === 1) this.banner = data.arr;
      });
    },
    getNotice() {
      this.$axios.get("/getNotice").then(res => {
        const { data } = res;
        if (data.status === 1) {
          this.notice = data.data;
          this.showNotice = true;
        }
      });
    },
    gotoArticle(id) {
      this.$router.push(`/article?id=${id}`);
    },
    goto(to) {
      to != "/" && this.$router.push(to);
    },
    getCategory() {
      const { cartList } = this.$store.state;
      this.$axios.get("/getCategory").then(res => {
        const { data } = res;
        if (data.status !== 1)
          return Notify({ type: "danger", message: "category!" });
        let arr = data.arr;
        arr.forEach((item, index) => {
          item.text = item.name;
          item.value = index;
          item.pro.forEach(el => {
            if (el.content) el.content = this.formatContent(el.content);
            el.selNum = 0;
          });
        });
        //categoryselNum
        cartList.forEach((ct, index) => {
          let has = false;
          arr.forEach(item => {
            const proHas = item.pro.findIndex(el => ct.id == el.id);
            if (proHas >= 0) {
              has = true;
              //
              if (ct.selNum > item.pro[proHas].num) {
                item.pro[proHas].selNum = item.pro[proHas].num;
              } else {
                item.pro[proHas].selNum = ct.selNum;
              }
            }
          });
          //arr
          if (!has) {
            cartList.splice(index, 1, {});
          }
        });
        const cartLists = cartList.filter(item => item.id);
        this.cartListSave(cartLists);
        this.nowIndexb = this.$store.state.nowIndex;
        this.category = arr;
        this.nowProducts = arr[this.nowIndexb].pro;
        this.nowProduct = arr[this.nowIndexb].pro[0];
        //
        this.calcYouhui();
        this.sortStart();
      });
    },
    getZhutu(item) {
      if (item.zhutu) return this.domain + item.zhutu;
      const pic = item.pic.split(",");
      return this.domain + pic;
    },
    numAdd(item) {
      if (item.selNum < item.num) {
        item.selNum += 1;
      } else {
        return Notify({ type: "warning", message: "!" });
      }

      let cartList = this.cartList;
      const has = cartList.findIndex(el => el.id == item.id);
      if (has >= 0) {
        cartList.splice(has, 1, item);
      } else {
        cartList.push(item);
      }
      let category = this.category;
      category[this.nowIndex].pro = this.nowProducts;
      this.category = category;
      this.cartListSave(cartList);
    },
    numRed(item) {
      let cartList = this.cartList;
      const has = cartList.findIndex(el => el.id == item.id);
      if (item.selNum > 0) {
        item.selNum -= 1;
        cartList.splice(has, 1, item);
      }
      if (item.selNum === 0) cartList.splice(has, 1);
      let category = this.category;
      category[this.nowIndex].pro = this.nowProducts;
      this.category = category;
      this.cartListSave(cartList);
    },
    showDetail(item) {
      let pro = item;
      pro.pics = item.pic.split(",");
      this.nowProduct = pro;
      this.showDetailVisible = true;
    },
    formatContent(content) {
      if (content.indexOf(this.domain) < 0) {
        content = content.replace(/\/Public/gi, this.domain + "/Public");
      }
      return content;
    },
    calcYouhui() {
      const { setting } = this.$store.state;
      let youhui = [];
      const firstOrderSale = parseInt(setting.firstOrderSale);
      if (firstOrderSale > 0) {
        youhui.push(`${firstOrderSale}`);
      }
      if (setting.countMoLing) {
        const arr = setting.countMoLing.split(",");
        arr.forEach(el => {
          let xx = el.split("-");
          youhui.push(`${xx[0]}${xx[1]}`);
        });
      }
      this.youhui = youhui;
    },
    sort(index) {
      let sortConfig = this.sortConfig;
      if (sortConfig[index].active == false) {
        sortConfig.forEach(it => {
          it.active = false;
        });
        sortConfig[index].active = true;
        this.sortConfigSave(sortConfig);
        this.sortStart();
      }
    },
    sortStart() {
      const i = this.sortConfig.findIndex(item => item.active == true);
      if (i === 0 || i === 1 || i === 2) {
        switch (i) {
          case 0:
            this.nowProducts.sort(this.compare("price"));
            break;
          case 1:
            this.nowProducts.sort(this.compare("sales"));
            break;
          case 2:
            this.sortChinese();
            break;
        }
      }
    },
    compare(property) {
      return function(a, b) {
        var value1 = a[property];
        var value2 = b[property];
        if (property == "price") return value1 - value2;
        return value2 - value1;
      };
    },
    sortChinese() {
      const dataLeven = "name";
      function getValue(option) {
        //  option 
        if (!dataLeven) return option;
        var data = option;
        dataLeven.split(".").filter(function(item) {
          data = data[item];
        });
        return data + "";
      }
      this.nowProducts.sort(function(item1, item2) {
        return getValue(item1).localeCompare(getValue(item2), "zh-CN");
      });
    }
  },
  computed: {
    ...mapState([
      "domain",
      "cartList",
      "cartNums",
      "sortConfig",
      "nowIndex",
      "setting",
      "totalPrice",
      "baseColor",
      "baseBgColor",
      "color",
      "baseBdColor"
    ]),
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  watch: {
    nowIndexb() {
      this.nowIndexSave(this.nowIndexb);
      this.nowName = this.category[this.nowIndexb].name;
      this.nowProducts = this.category[this.nowIndexb].pro;
      window.scrollTo(0, 0);
      this.sortStart();
    }
  }
};
</script>

<style lang="less" >
@borderCorle: #efefef;
@baseColor:#f69e2a;
.clearfix:after {
visibility: hidden;
display: block;
font-size: 0;
content: " ";
clear: both;
height: 0;
}
.basecolor {color: @baseColor}
.swiper-slide {
  img {
    max-width: 100%;
  }
}
.switchItem {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid @borderCorle !important;
  border-bottom: 1px solid @borderCorle !important;
  background: #fff
}
.youhui{
  display: flex;
  align-items: center;
  span{
    font-size: 11px;
    margin: 0 3px
  }
}
.van-dropdown-menu {
  height: 40px !important;
  width: 18%;
  border: none !important;
  border-right: 1px solid @borderCorle !important;
  padding-right: 10px;
}
.van-dropdown-menu__title {
  font-size: 12px !important;
}
.sortWrap {
  font-size: 12px;
  display: flex;
  align-items: center;
  span {
    margin-right: 8px;
    i {
      top: 2px;
      position: relative;
    }
  }
  span.active{
    color: #f69e2a
  }
}
.productList {
  background: #f1f2f3;
  padding: 2% 0;
  .productItem {
    width: 45.5%;
    float: left;
    margin-left: 3%;
    background-color: #fff;
    margin-bottom: 2.5%;
    padding-bottom: 1.5%;
  }
  &:after {
    content: "";
    width: 45.5%;
    height: 0;
    display: block;
  }
}
.text {
    padding: 0 5% 2% 5%;
    .title {
    font-size: 13px;
    font-weight: bold;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: 0;
    }
    .note {
    font-size: 11px;
    color: #777;
    padding: 5px 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    height: 15px;
    }
    .price {
    font-size: 16px;
    color: #f69e2a;
    font-weight: 700;
    }
}
.text.detailText{
    margin-top: 15px;
    .title{
        span{
            font-size: 18px;
            font-weight: bold
        }
    }
}
.priceAndNums{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .numbers{
        display: flex;
        align-items: center;
        justify-content: space-around;
        span{
            font-size: 15px;
            margin: 0 5px;
            color: #666
        }
        i{
            font-size: 18px;
            opacity: .6;
        }
    }
}
.detailModal{
    margin-top: 50px;
}
.detailDetail{
    border-top: 10px solid #eee;
    padding: 0 20px;
    img{
        max-width: 100%;
    }
}
.youhuiModal{
  margin: 30px 0 20px 0;
  h4{
    text-align: center
  }
  .youhuiItem{
    margin: 10px 20px;
    span{
      margin-right: 10px
    }
  }
}
.outsideWrap{
  height: 100%;
  padding: 0 !important;
}
.modeType1{
  height: 62%;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
  .sidebar{
    height: 100%;
    overflow:scroll;
    width: 22%;
    .van-sidebar-item{
      padding: 10px 12px 10px 8px !important;
      font-size: 14px !important;
      text-align: center;
      border-left: none;
      background: #f69e2a !important ;
      color: #fff;
    }
    .van-sidebar-item--select{
      background: #fff !important;
      color: #f69e2a
    }
  }
  .modeType1-right{
    width: 76%;
    overflow: scroll;
    position: relative;
    padding-bottom: 25px;
    height: calc(100% - 25px);
    .switchItem{
      padding: 10px 0;
      margin-bottom: 10px;
      border-top: none !important;
    }
    .productList{
      background: #fff;
      padding: 0;
      .productItem{
        float: none;
        width: 100%;
        margin: 0;
        padding: 10px 0;
        .image{
          float: left;
          width: 75px;
        }
        .text{
          padding: 0;
          float: left;
          width: calc(96% - 75px);
          margin-left: 2%;
        }
      }
    }
  } 
}
.homeFooter{
  position: fixed;
  z-index: 999;
  left: 0;
  right: 0;
  bottom: 0;
  height: 50px;
  background: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .item{
    width: 23%;
    text-align: center;
    position: relative;
    p{
      font-size: 12px;
      margin: 0;
    }
    .cartNums{
      position: absolute;
      font-size: 10px;
      background: @baseColor;
      color: #fff;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      right: 30%;
      top: 0;
      z-index: 99;
    }
  }
}
.modeType1-search-user{
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ddd;
  border-top: 1px solid #ddd;
  .mt1-search{
    width: 72%;
    .search{
      padding:3px 5px;
      .van-search__content{
        background: #fff !important
      }
      
    }
  }
  .mt1-user{
    width: 28%;
    background: #eee;
    font-size: 14px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #666;
    font-weight: bold;
    i{
      font-size: 20px;
      margin-right: 6px;
      font-weight: normal;
    }
  }
}
.mt1-footer{
  position: fixed;
  z-index: 9999;
  left: 0;
  right: 0;
  bottom: 0;
  height: 50px;
  background: #404040;
  display: flex;
  .mt1-cart{
    width: 68%;
    display: flex;
    align-items: center;
    .iconfont{
      color: @baseColor;
      font-size: 30px;
      margin-left: 10px;
    }
    span{
      margin-left: 10px;
      color: #d4d4d4
    }
    span.totalPrice{
      color: #fff;
      font-size: 22px;
    }
    .cartNums{
      position: absolute;
      width: 20px;
      height: 20px;
      font-size: 12px;
      background: @baseColor;
      color: #fff;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-style: normal;
      top: 2px;
      left: 28px;
    }
  }
  p{
    width: 32%;
    height: 50px;
    color: #fff;
    background: @baseColor;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
