<template>
  <div class="userWrap">
    <div class="bg"></div>
    <Navbar name="" />
    <div class="quitWrap">
      <van-field
        v-model="content"
        type="textarea"
        :error-message="infoErrorMsg"
        required
        placeholder=""
        class="input"
        rows="4"
        @blur="infoBlur"
      />
      <van-field v-model="phone" required placeholder="" class="input" />
      <van-button type="warning" class="quitBtn" round block @click="submit"></van-button>

      <div class="fbList">
        <h5 class="title"></h5>
        <van-divider />
        <div v-for="item in list" :key="JSON.stringify(item)" class="fbItem">
          <p class="time">{{item.time}}</p>
          <p class="content">{{item.content}}</p>
          <p class="return">{{item.return}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Notify } from "vant";
import { mapState } from "vuex";
import Navbar from "./Navbar";
export default {
  name: "Feedback",
  components: {
    Navbar
  },
  data() {
    return {
      content: "",
      infoErrorMsg: "",
      phone: "",
      list: []
    };
  },
  created() {},
  mounted() {
    this.getFeedback();
  },

  methods: {
    infoBlur() {
      if (this.content.length < 10) {
        this.infoErrorMsg = "";
      } else {
        this.infoErrorMsg = "";
      }
    },
    getFeedback() {
      const user = this.user;
      this.$axios.get("/getFeedback", { params: { user } }).then(({ data }) => {
        console.log(data);
        if (data.arr && data.arr.length) {
          this.list = data.arr;
        }
      });
    },
    submit() {
      const content = this.content;
      const phone = this.phone;
      if (!/^1[3456789]\d{9}$/.test(phone)) {
        return Notify({ type: 'warning', message: '' });
      }
      if(content.length < 10){
        return Notify({ type: 'warning', message: '' });
      }
      const user = this.user
      this.$axios.get("/subFeedback",{params:{user,content,phone}}).then(({data})=>{
        if(data.status === 1){
          Notify({ type: 'success', message: '' });
          return this.$router.go(0)
        }else{
          return Notify({ type: 'danger', message: '' });
        }
      })
    }
  },
  computed: {
    ...mapState(["user"])
  },
  watch: {}
};
</script>

<style lang="less" scope>
.quitWrap {
  width: 95%;
  padding: 25px 0;
  margin: 0 auto;
  .input {
    border-radius: 5px;
    margin-bottom: 10px;
  }
}
.quitBtn {
  transform: scale(0.9);
  margin-top: 10px;
}
.fbList {
  h5 {
    margin: 0;
  }
  .title {
    padding: 8px 0;
    border-bottom: 1px #ddd solid;
    font-size: 13px;
    margin-top: 15px;
  }
  .fbItem {
    padding: 10px 0;
    .time {
      font-size: 12px;
      color: #999;
    }
    .content {
      font-size: 13px;
      color: #666;
      padding: 5px 0;
    }
    .return {
      font-size: 13px;
      color: #5675f2;
    }
  }
}
</style>
