<template>
  <div style="padding-bottom:50px;" :class="setting.modeType == 1 ? 'outsideWrap' : ''">
    <transition name="fade" mode="out-in">
      <router-view></router-view>
    </transition>
    <van-overlay :show="showNotWechat" class="isNotWechat">
      <div class="wrapper" @click.stop>
        <h3></h3>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import qq from "qq";
import { Dialog } from "vant";
import { Notify } from "vant";
import { mapMutations, mapState } from "vuex";
export default {
  name: "Index",
  components: {},
  data() {
    return {
      nums: 0,
      showNotWechat: false
    };
  },
  created() {
    // this.checkIsWechat();
    const { shopCode } = this.$store.state; //storeshopCode
    if (!shopCode) {
      return Notify({ type: "danger", message: "shopCode!" });
    }
    this.getSetting();
  },
  mounted() {
    
    this.calcCartListNum();
  },

  methods: {
    ...mapMutations(["settingSave", "cartNumsSave", "totalPriceSave"]),
    //
    checkIsWechat() {
      var ua = window.navigator.userAgent.toLowerCase();
      if (ua.match(/MicroMessenger/i) != "micromessenger") {
        //
        this.showNotWechat = true;
      }
    },
    getSetting() {
      this.$axios.get("/getSetting").then(res => {
        const { data } = res;
        if (data.arr.open != "1") {
          this.$router.push("/close");
        }
        this.settingSave(data.arr);
        data.arr.outside > 0 && this.calcDistance();
      });
    },
    calcDistance() {
      const _this = this;
      const { latitude: shopLat, longitude: shopLng } = this.setting;
      const outsideKm = parseFloat(this.setting.outsideKm) + 0.5;
      var geolocation = new qq.maps.Geolocation(
        "S6HBZ-4CSCX-XP74J-7TIBY-CAZXE-QKF6E",
        "good"
      );
      geolocation.getLocation(
        ({ lat, lng }) => {
          console.log(lat, lng);
          const distance = _this.GetDistance(lat, lng, shopLat, shopLng);
          console.log("distance " + distance);
          console.log("outsideKm " + outsideKm);
          if (distance > outsideKm) {
            Dialog.alert({
              title: "",
              message: ""
            });
          }
        },
        error => {
          console.log(error);
        },
        { timeout: 8000 }
      );
    },
    GetDistance(lat1, lng1, lat2, lng2) {
      var radLat1 = (lat1 * Math.PI) / 180.0;
      var radLat2 = (lat2 * Math.PI) / 180.0;
      var a = radLat1 - radLat2;
      var b = (lng1 * Math.PI) / 180.0 - (lng2 * Math.PI) / 180.0;
      var s =
        2 *
        Math.asin(
          Math.sqrt(
            Math.pow(Math.sin(a / 2), 2) +
              Math.cos(radLat1) *
                Math.cos(radLat2) *
                Math.pow(Math.sin(b / 2), 2)
          )
        );
      s = s * 6378.137; // EARTH_RADIUS;
      s = Math.round(s * 10000) / 10000;
      return s;
    },
    calcCartListNum() {
      let cartNums = 0;
      let totalPrice = 0.0;
      this.cartList.forEach(el => {
        cartNums += el.selNum;
        totalPrice += el.selNum * parseFloat(el.price);
      });
      this.cartNumsSave(cartNums);
      this.totalPriceSave(totalPrice);
    }
  },
  computed: {
    ...mapState(["setting", "cartList", "cartNums"])
  },
  watch: {
    $route: {
      handler: function(route) {
        this.currentRouter = route.name;
      },
      immediate: true
    }
  }
};
</script>

<style lang="less">

body {
  margin: 0;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "", Arial, sans-serif;
}
ul {
  margin: 0;
  padding: 0;
}
ul li {
  list-style: none;
}
#app {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
p {
  margin: 0;
}
.van-nav-bar__title.van-ellipsis {
  font-size: 14px;
}
.van-icon-arrow-left::before {
  color: #323233;
}
.noCartListDiv {
  text-align: center;
  padding-top: 50px;
  font-size: 14px;
  color: #999;
  .iconfont {
    font-size: 30px;
  }
  p {
    margin-top: 10px;
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.van-divider {
  margin: 0 !important;
}
.isNotWechat {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  h3 {
    font-weight: normal;
  }
}
</style>
